import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            search: '',
            title: '全部模板',
            page: 1,
            haveMore: true,
            list: [],
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
            type: this.$route.query.type
        }
    },
    created(){
        this.$store.dispatch('pushserviceFollowUp',null)
        this.pull(false)
        if(this.type == 1){
            this.title = '随访服务'
        }
        else if(this.type == 2){
            this.title = '面诊预约'
        }
        else if(this.type == 3){
            this.title = '家庭医生'
        }
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        pull(h){
            if(this.haveMore){
                this.$post('PlatFormAPI/ServicePackItem/QueryServicePackTempletByPage',this.$pick({
                    TempletType: this.type,
                    PackTempletName: this.search,
                    PageIndex: this.page,
                    PageSize: 15,
                })).then(o => {
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        send(i){
            this.$get('PlatFormAPI/ServicePackItem/QueryServicePackTempletAndDetail',{
                PackTempletId: i.PackTempletId
            }).then(o => {
                this.$store.dispatch('pushDisease', [])
                this.$store.dispatch('pushserviceBase', null)
                this.$store.dispatch('pushservice', o)
                this.$store.dispatch('pushserviceCont', null)
                this.$router.push('/create-service-base?fromTemp=1')
            })
        },
        searchFor(){
            this.haveMore = true
            this.page = 1
            this.pull(false)
        }
    }
}



